import React from 'react';
import './TeslaNotice.css';

const TeslaNotice = () => (
    <div className="tesla-battery__notice">
        <p>
            The actual amount of range that you experience will be very based on your particular use conditions. See how particular use conditions may affect your rangein our simulation model.
        </p>
        <p>
            Vehicle range may vary depending on the vehicle configuration, battery age and condition, driving style and operating, environmental and climate conditions.
        </p>
    </div>
)

export default TeslaNotice;


